import './thumbnails-section.scss';
import ClipboardJS from 'clipboard';
import LinkDropdown from '../../../modules/link-dropdown/link-dropdown';
import ThumbTabs from '../../../modules/thumbTabs/thumbTabs';

const linkDropdown = new LinkDropdown({
	dropdownClass: 'thumbnails__form-select',
});
linkDropdown.init();

const thumbTabs = new ThumbTabs({
	containerSelector: '.thumbnails__form',
	btnSelector: '.thumbnails__form-button',
	tabSelector: '.thumbnails__form-content',
	imageSelector: '.thumbnails__preview-image',
	contentBtnSelector: '.thumbnails__content-card',
	linkSelector: '.thumbnails__preview-link',
});
thumbTabs.init();

const clipboard = new ClipboardJS('.clipboard');
const defaultMessage = document.querySelector('.thumbnails__copy-button-text.default');
const successMessage = document.querySelector('.thumbnails__copy-button-text.success');
const errorMessage = document.querySelector('.thumbnails__copy-button-text.error');

clipboard.on('success', () => {
	console.log('success');
	defaultMessage.classList.remove('active');
	successMessage.classList.add('active');
	setTimeout(() => {
		successMessage.classList.remove('active');
		defaultMessage.classList.add('active');
	}, 2000);
});

clipboard.on('error', () => {
	console.log('error');
	defaultMessage.classList.remove('active');
	successMessage.classList.add('active');
	setTimeout(() => {
		successMessage.classList.remove('active');
		defaultMessage.classList.add('active');
	}, 2000);
});
